import React, { Component, Fragment } from 'react'
import { withStyles } from '@material-ui/styles'
import HeaderTop from '../Components/HeaderTop'
import {Provider} from '../Context'
import IntroStep from '../Components/Steps/IntroStep'
import SelectCourse from '../Components/Steps/SelectCourse'
import CreateCourse from '../Components/Steps/CreateCourse'
import AlertDialog from '../Components/AlertDialog'



const styles = theme =>({
    IntroStepWrapper:{
        height:"calc(100vh - 200px)",
        display:"flex",
        justifyContent:"center",
        flexDirection:"column"
    },
    stepTitle:{
        textAlign:"center"
    },
    stepHelper:{
        textAlign:"center"
    },
    buttonWrapper:{
        textAlign:"center",
        padding:10
    },
    createNewBtn:{
        backgroundColor:window.props.colors.secondColor
    },
    createPageWrapper:{
        padding:"0px 50px 20px 50px" 
    },
    courseListWrapper:{
        maxWidth:"800px",
        margin:"0 auto"
    },
    courseElementWrapper:{
        margin:"20px 0px",
        borderRadius:10,
    },
    courseElementtitle:{
        color:theme.palette.primary.main,
        textTransform:"uppercase"
    },
    courseElementInfo:{
        padding:30
    },
    flexCenter:{
        display:"flex",
        justifyContent:"center",
        flexDirection:"column",
        alignItems:"center"
    },
    choseBtn:{
        borderTopRightRadius:10,
        borderBottomRightRadius:10,
        borderBottomLeftRadius:0,
        borderTopLeftRadius:0,
        height:"100%",
        position:"relative",
        right:"-7px"
    },
    dialogButtons:{
        color:"white!important",
        "&:hover":{
            color:theme.palette.primary.main+"!important"
        }
    }
})


const newCourseBasis={
    course_grade:50,
    course_pacing:"instructor_paced",
    course_settings:[],
    startDate:new Date(),
    endDate:new Date(new Date().setMonth(new Date().getMonth()+3))

}


class CreatePage extends Component{
    state={
        translations:window.props.translations,
        courses:window.props.courses || [],
        step:0,
        selectedCourse:undefined,
        mode:"new-course",
        newCourse:newCourseBasis,
        openDialog:false,
        newCourseLink:undefined
    }

    componentWillMount(){
        const referredCourseId = this.props.match.params.referredCourseId
        if(referredCourseId){
         const selectedCourse = this.state.courses.find((course)=>course.course_id===referredCourseId)
         this.setCourse(selectedCourse) 
         this.setState({mode:"re-run", step:1})
        }
    }
    

    setMode=(mode)=>{
        this.setState((prevState)=>({
            mode,
            step:prevState.step+1,
        })
        )
    }

    setCourse=(selectedCourse)=>{
        this.setState((prevstate)=>({
            selectedCourse,
            newCourse:{
                ...prevstate.newCourse,
                course_name:selectedCourse.course_name, 
                course_organisation:selectedCourse.course_organisation
            }
        }))
    }

    addCourseDetails = (courseDetail)=>{
        this.setState((prevstate)=>({
            newCourse:{...prevstate.newCourse, [courseDetail.name]:courseDetail.value }
        }))
    }

    getContext=()=>{
        return{
            ...this.state,
            handleFieldChange:this.handleFieldChange,
            handleNextClick:this.handleNextClick,
            addCourseDetails:this.addCourseDetails,
            toggleDialog:this.toggleDialog,
            handleBackClick:this.handleBackClick,
            launchCourseCreation:this.launchCourseCreation
        }
    }

     handleNextClick= () =>{
        this.setState((prevState)=>({
            step:prevState.step+1
        })
        )
    }

    launchCourseCreation = () =>{
        this.handleNextClick();
        setTimeout(()=>{
            this.setState({newCourseLink:"https://studio.qualif-phileas.themoocagency.com/course/course-v1:asia+431+Vodeclic_en"})
        }, 2000);
        
    }

    handleBackClick = () =>{
        this.setState((prevState)=>({
            step:prevState.step-1
        }))
    }

    handleFieldChange=(fieldName, fieldValue)=>{
        this.setState((prevState) => {
            return {newCourse:{ ...prevState.newCourse, [fieldName]:fieldValue}};
        });
    }

    toggleDialog = () =>{
        this.setState((prevstate)=>(
            {
                openDialog:!prevstate.openDialog
            }
        ))
    }

    eraseNewCourse=()=>{
        this.setState({
            newCourse:newCourseBasis,
            step:0,
            openDialog:false,
            selectedCourse:undefined,
            mode:undefined,
        })
    }

    

    render(){
        const {classes} = this.props,
            {step, mode, selectedCourse, translations}=this.state
        const courses = this.state.courses.filter((course)=>course.type==="phileas")
        console.log(this.state)
        return(
            <Fragment>
                <Provider value={this.getContext()}>
                    <HeaderTop/>
                    <div className={classes.createPageWrapper}>
                        {step===0 && <IntroStep classes={classes} setMode={this.setMode}/>}
                        {(step===1 && mode==="re-run" && !selectedCourse) && <SelectCourse courses={courses} classes={classes} setCourse={this.setCourse}/> }  
                        {([1,2,3,4,5,6].includes(step) && (mode==="new-course"|| selectedCourse) ) && <CreateCourse/> }
                    </div>
                    <AlertDialog 
                        openDialog={this.state.openDialog}
                        toggleDialog={this.toggleDialog}
                        dialogTitle={translations.cancelCreationTitle}
                        dialogContent={translations.cancelCreationContent}
                        dialogValid={translations.validCancelCreation}
                        dialogCancel={translations.cancel}
                        dialogAction={this.eraseNewCourse}
                        classes={classes}
                    />
                </Provider>
            </Fragment>
        )
    }
}




export default withStyles(styles)(CreatePage);