import React, {Component} from 'react'
import Loader from 'react-loader-spinner';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Context } from '../../Context';
import CheckCircleOutline from '@material-ui/icons/CheckCircleOutline';


export default  class LoadingPage extends Component{
    static contextType = Context
    render(){
        const {newCourseLink, eraseNewCourse, newCourse} = this.context,
         {classes, translations}=this.props
        console.log(this.props)
        return(
            <div className={classes.loadingPage}>
            {newCourseLink ?
                <>
                    <div><CheckCircleOutline style={{fontSize:90, color:window.props.colors.mainColor}}/></div>
                    <h3>{translations.courseHasBeenCreated}</h3>
                    <h4>{translations.stepFields.course_name_title} : {newCourse.course_name}</h4>
                    <Grid container 
                        spacing={6}
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <Grid item>
                            <Button href={newCourseLink} onClick={()=>eraseNewCourse()}>
                                {translations.seeMyCourse}
                            </Button>
                        </Grid>
                        <Grid item>
                            <Button href="/" onClick={()=>eraseNewCourse()}>
                                {translations.backToHomePage}
                            </Button>
                        </Grid>
                    </Grid>
                </>
                :<>
                    <h3>{translations.waitCourseCreation}</h3>
                    <Loader 
                    type="Puff"
                    color={window.props.colors.mainColor}
                    height="100"	
                    width="100"
                    />   
                </>
               
            }
        </div>
        )
    }
}  


