import React from 'react';
import Moment from 'react-moment';
import 'moment/locale/fr';


function CourseSummary({newCourse, translations,classes}) {
  const getCourseInfoValue = (courseInfo)=>{
    let courseInfoDetails;
    if(Array.isArray(courseInfo[1]) && courseInfo[1].length>0){
        courseInfoDetails=
            <ul>
                {courseInfo[1].map((info, index)=><li key={index}>{translations.stepFields[courseInfo[0]+"_"+info]+" " || info+ " "}</li>)}
            </ul>
    }
    else if(courseInfo[1] instanceof Date){
        courseInfoDetails= <Moment locale={window.props.currentLanguage==="fr"? "fr" : "en"} format={"LLL"}>{courseInfo[1].toString()}</Moment>
    }
    else if(courseInfo[0]==="course_grade"){
        courseInfoDetails= newCourse.course_settings.includes('graded')?<span>{courseInfo[1]}%</span> : <span>{translations.courseNotGraded}</span>
    }
    else{
        courseInfoDetails= <span>{translations.stepFields[courseInfo[0]+"_"+courseInfo[1]] || courseInfo[1]}</span>
    }

    if(courseInfoDetails){
      return (
        <>
            <span className={classes.courseSummaryTitle}>{translations.stepFields[courseInfo[0]+"_title"]} : </span> 
            {courseInfoDetails}
        </>
        )  
    }
  }  

  return (
    <div>
        <h3>{translations.verifyCourseInfo}</h3>
        {Object.entries(newCourse).map((courseInfo, key)=>
            <div key={key}>
                {getCourseInfoValue(courseInfo)}
            </div>)}
    </div>    
  );
}

export default CourseSummary;